import { PAGE_SIZE } from '@/assets/config'
import { CODE, SelectDataSource, researchProjectsKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'researchProjectsOther',
  data () {
    return {
      code: CODE.ResearchProjectsOther,
      dataKeys: researchProjectsKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '立项时间', key: 'start_time', type: 'date' },
        { label: '项目名称', key: 'name' },
        { label: '项目人员', key: 'people' },
        { label: '角色', key: 'role', selectOption: SelectDataSource.Roles },
        { label: '项目来源', key: 'source', selectOption: SelectDataSource.ProjectStatus },
        { label: '获批经费', key: 'funds' },
        { label: '项目批准号', key: 'code' },
        { label: '项目状态', key: 'project_status', selectOption: SelectDataSource.ProjectStatus }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables(this.page)
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.ResearchProjectsOther.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.ResearchProjectsOther.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/researchProjects/other/new', '科研项目(其他纵向项目)-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/researchProjects/other/' + item.id, '科研项目(其他纵向项目)-详情-' + item.id)
    }
  },
  created () {
    this.operation = Cache.pageOperation('academic_achievements')
    this.searchOptions[4].selectOption = ['地、市、厅、局等政府部门项目', '国际合作研究项目', '与港澳台合作研究项目',
      '外资研究项目', '其他研究项目', '学校社科项目']
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



